*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

section {
  overflow-x: hidden;
}

.container {
  max-width: 1094px;
  margin: 0 auto;
  padding: 0 1em;
  display: block;
}

.link {
  white-space: nowrap;
  color: inherit;
  text-decoration: none;
  transition-property: color;
  transition-duration: .2s;
  transition-timing-function: ease;
}

.link:hover {
  color: #90ee90;
}

.btn {
  font-size: inherit;
  color: #fff;
  background-color: inherit;
  border: none;
  outline: none;
  padding: 1em 1.625em;
  font-weight: bold;
  transition-property: color, background-color;
  transition-duration: .2s;
  transition-timing-function: ease;
  display: inline-block;
}

.btn_green {
  background-color: #42ae60;
  border-radius: 6px;
}

.btn_green:hover {
  color: #fff;
  background-color: #87bd1d;
}

.header {
  color: #fff;
  background-color: #273720de;
  padding: 2.5em 0;
}

.header__wrapper {
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: ". header__nav header__btns";
  align-items: center;
  display: grid;
}

.header__nav {
  grid-area: header__nav;
  gap: 3em;
  display: flex;
}

.header__btns {
  grid-area: header__btns;
  justify-content: flex-end;
  margin-left: 2.5em;
  display: flex;
}

.intro {
  color: #fff;
  background-color: #273720;
  padding: 60px 0;
  display: flow-root;
  overflow: hidden;
}

.intro__left-plus-right {
  grid-template-columns: 450px auto;
  display: grid;
}

.intro__right {
  position: relative;
  right: -100px;
}

.intro__right-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.intro__right-img_hidden {
  opacity: 0;
}

.intro__title {
  margin: 0 0 40px;
  font-size: 4.5em;
  font-weight: normal;
  line-height: 1;
}

.intro__description {
  margin-bottom: 30px;
  font-size: 1.2em;
}

.intro__wrapper {
  flex-direction: column;
  gap: 180px;
  display: flex;
}

.intro__companies {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  display: flex;
}

.intro__companies-text {
  width: 200px;
}

.intro__companies-images {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.intro__companies-text {
  text-align: center;
}

.deals {
  color: #273720;
  padding: 80px 0;
}

.section-header {
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 25px;
  display: flex;
}

.section-header__subtitle {
  text-transform: uppercase;
  margin: 0;
}

.section-header__title {
  text-align: center;
  margin: 0;
  font-size: 4em;
  font-weight: normal;
}

.section-header__description {
  margin: 0;
}

.lines-text {
  width: 470px;
  flex-direction: column;
  font-size: 31px;
  display: flex;
}

.lines-text__item {
  border-top: 1px solid #273720;
  padding: .8em 0;
}

.lines-text__item:last-child {
  border-bottom: 1px solid #273720;
}

.deals__two {
  justify-content: space-between;
  align-items: center;
  margin-top: 76px;
  display: flex;
}

.vendors {
  color: #273720;
  padding: 80px 0;
  overflow: hidden;
}

.vendors__two {
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  margin-top: 140px;
  display: flex;
}

.vendors__two-right {
  max-width: 523px;
  position: relative;
}

.vendors__two-right-img_absolute {
  position: absolute;
  top: -28%;
  left: -7%;
}

.vendors__two-right-line {
  width: 150%;
  height: 174%;
  clip-path: polygon(7% 100%, 6% 66%, 42% 0%, 66% 1%, 64% 38%, 31% 100%);
  background-color: #e6ff4c;
  position: absolute;
  bottom: 13%;
  left: 30%;
}

.teams {
  background-color: #273720;
  padding: 120px 0;
}

.teams__title {
  color: #fff;
}

.teams__wrapper {
  flex-direction: column;
  gap: 57px;
  display: flex;
}

.teams__slider {
  max-width: 100%;
  overflow: initial;
}

.teams__slider-items {
  align-items: center;
}

.teams__slider-item {
  --width: max(75%, 500px);
  min-width: var(--width);
  max-width: var(--width);
  border-radius: 30px;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  padding: 2.75em 5.125em;
  display: flex;
  overflow: hidden;
}

.teams__slider-item_odd {
  background-color: #fff;
}

.teams__slider-item_even {
  background-color: #d2ebd5;
}

.teams__slider-arrows {
  -webkit-user-select: none;
  user-select: none;
  justify-content: center;
  gap: 3em;
  margin-top: 3em;
  display: flex;
}

.teams__slider__arrow {
  cursor: pointer;
  padding: 20px;
  transition: background-color .2s;
}

.teams__slider__arrow:hover {
  background-color: #ffffff1a;
}

.teams__slider-item-img {
  --width: 128px;
  min-width: var(--width);
  max-width: var(--width);
}

.teams__slider-text {
  margin: 0;
  font-size: 2em;
}

.teams__slider-review {
  align-items: center;
  gap: 24px;
  display: flex;
}

.teams__slider-review-img {
  --width: 64px;
  min-width: var(--width);
  max-width: var(--width);
}

.teams__slider-review-text {
  font-size: 1.125em;
}

.security {
  color: #fff;
  background-color: #273720;
  padding: 120px 0;
}

.security__wrapper {
  position: relative;
}

.security__title {
  margin-bottom: 20px;
}

.security__img-bg {
  width: 250px;
  max-width: none;
  max-height: none;
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-30%, -25%);
}

.security__section-header {
  z-index: 2;
  position: relative;
}

.footer {
  color: #fff;
  background-color: #273720;
  padding: 80px 0;
}

.footer__wrapper {
  justify-content: space-between;
  display: flex;
}

.footer__columns {
  justify-content: space-between;
  gap: 4em;
  padding-right: 10vw;
  display: flex;
}

.footer__column {
  flex-direction: column;
  gap: 1em;
  display: flex;
}

.footer__column-item {
  gap: 1.5em;
  display: flex;
}

.footer__column-item_head {
  font-size: 2em;
}

.footer__logo-wrapper {
  min-width: max-content;
}

@media (max-width: 1000px) {
  .deals__two {
    flex-direction: column;
    gap: 80px;
  }

  .intro__companies {
    justify-content: center;
  }

  .lines-text {
    width: auto;
  }

  .vendors__two {
    flex-direction: column;
    gap: 160px;
    margin-top: 76px;
  }
}

@media (max-width: 720px) {
  .header__wrapper {
    grid-template-columns: auto;
    grid-template-areas: "header__nav"
                         "header__btns";
    gap: 2em;
  }

  .header__nav {
    flex-direction: column;
    align-items: center;
    gap: 2em;
  }

  .header__btns {
    flex-direction: column;
    align-items: center;
    margin-left: 0;
  }

  .intro__wrapper {
    gap: 90px;
  }

  .intro__left-plus-right {
    grid-template-columns: auto;
    place-items: center;
    gap: 4em;
  }

  .intro__right {
    position: static;
    right: auto;
  }

  .intro__left {
    max-width: 400px;
  }

  .intro__title {
    margin-bottom: 20px;
    font-size: 3em;
  }

  .intro__description {
    margin-bottom: 20px;
    font-size: 1.1em;
  }

  .teams__slider-item {
    --width: max(75%, 200px);
    padding: 1em 2em;
  }

  .teams__slider-text {
    font-size: 1em;
  }

  .teams__slider-review-text {
    font-size: .8em;
  }

  .section-header__title {
    font-size: 2em;
  }

  .security__img-bg {
    width: 200px;
  }
}

@media (max-width: 680px) {
  .footer__wrapper {
    flex-direction: column;
    align-items: center;
    gap: 4em;
  }

  .footer__columns {
    flex-wrap: wrap;
    justify-content: center;
    padding-right: 0;
  }
}

